.Modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    z-index: -1;
    background: var(--overlay-color);
}

.overlay {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s transform;
    transform: scale(0.5);
}

.content {
    padding: 20px;
    border-radius: 15px;
    background: white;
    transition: 0.3s transform;
    max-width: 60%;
    .titleBlock{
        margin-bottom: 10px;
        .title{
            font-size: 22px;
            font-weight: 700;
            line-height: 24px;
        }
    }
    @media (max-width: 600px) {
        max-width: 98%;
    }
}

.opened {
    pointer-events: auto;
    opacity: 1;
    z-index: 100;
    .overlay {
        transform: scale(1);
    }
}

.isClosing {
    .content {
        transform: scale(0.2);
    }
}
