.sidebarWrap{
  display: flex;
  align-items: center;

  .icoBlock {
    width: 40px;
    height: 28px;
    position: relative;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;

    span {
      display: block;
      position: absolute;
      height: 3px;
      width: 100%;
      background: #2d2c2c;
      border-radius: 9px;
      opacity: 1;
      left: 0;

      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 10px;
      }
      &:nth-child(3) {
        top: 20px;
      }
    }
  }

  .sidebarMenu{
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-index-sidebar-menu);
    height: 100vh;
    background-color: white;
    min-width: 320px;
    border-radius: 0 20px 20px 0;
    transform: translateX(-100%);
    transition: .4s;

    .title{
      font-size: 22px;
      border-bottom: 2px solid;
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }

  .overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: var(--z-index-sidebar-bg);
  }

  &.openMenu{
    .sidebarMenu{
      transform: translateX(0);
      transition: .4s;
    }
  }
}